<template>
    <div class="login-container">
        <p class="login-title">로그인</p>
        <form novalidate class="w-full" @submit.prevent="validate">
            <!--div class="input-box">
                <div class="label">아이디</div>
                <v-text-field
                        v-model="uid"
                        :error-messages="uidErrors"
                        outlined
                        @input="$v.uid.$touch()"
                        @blur="$v.uid.$touch()"
                        placeholder="아이디를 입력해주세요"/>
                <div class="label">비밀번호</div>
                <v-text-field type="password"
                              outlined
                              :error-messages="upassErrors"
                              @input="$v.upass.$touch()"
                              @blur="$v.upass.$touch()"
                              v-model="upass"
                              placeholder="비밀번호를 입력해주세요"/>
            </div>
            <div class="button-row" style="text-align: center">
                <v-btn type="submit" rounded color="blue" style="width:100%;margin-top:30px;height:60px;color:#fff;">
                    <span style="font-size: 14pt !important;">로그인</span>
                </v-btn>
            </div -->
            <!-- div class="find-box">
                <div class="right" @click="findPass()">
                    계정찾기
                </div>
            </div -->

        </form>
        <div class="button-row" style="text-align: center">
            <v-btn type="button" rounded color="blue" @click="zoomLogin()" style="width:50%;margin-top:70px;height:60px;color:#fff;">
                <span style="font-size: 14pt !important;">ZOOM 로그인</span>
            </v-btn>
        </div>
        <!--div class="divide-line" v-if="snsList.length>0"></div>
        <div class="sns-box" v-if="snsList.length>0">
            <ul class="sns-ul">
                <li class="sns-li" v-for="sns in snsList" :key="sns.code" @click="snsLogin(sns.code)">
                    <img :src="sns.img"><span>{{sns.title}}</span>
                </li>
            </ul>
        </div>
        <div class="button-row" style="width:100%;margin:auto;text-align: center">
            <span class="guide">아직 하모니카 회원이 아니신가요?</span>
            <span class="join-btn" @click="$router.push('/joinIntro')">회원가입</span>

        </div -->

    </div>
</template>
<script>
    import {validationMixin} from 'vuelidate'
    import {required} from 'vuelidate/lib/validators'
    import apiService from '@/config/web/apiService'
    import {
        API_URL_LOGIN,
        API_URL_GET_MEMBER_CONFIG,
    } from '@/config/web/urls'
    import AUTH from '@/config/web/auth'
    import {mapGetters} from "vuex";

    export default {
        mixins: [validationMixin],
        validations: {
            uid: {required},
            upass: {required},
        },
        name: 'Login',
        computed: {
            ...mapGetters('config', ["getSns", "getSettingActive"]),
            uidErrors() {
                const errors = []
                if (!this.$v.uid.$dirty) return errors
                !this.$v.uid.required && errors.push('아이디를 입력하세요')
                return errors;
            },
            upassErrors() {
                const errors = []
                if (!this.$v.upass.$dirty) return errors
                !this.$v.upass.required && errors.push('비밀번호를 입력하세요')
                return errors;
            }
        },
        watch: {
            getSettingActive(val) {
                if (val) {
                    this.settingSns();
                }
            }
        },
        data() {
            return {
                uid: '',
                upass: '',
                insertFlag: false,
                snsList: [],
                snsCode: '',
            };
        },
        created() {
            const member = AUTH.getSession();
            if (member && member?.memberInfo) {
                this.$router.push('/');
                return;
            }
            this.$eventBus.$on('zoomLoginResult', this.zoomLoginResult);
            if (this.getSettingActive) {
                this.settingSns()
            }

        },
        methods: {
            zoomLogin() {
                const zoomId = 'S6GcPGBYT2S6t0zNdDxXeQ'
                const redirectUrl = 'https://dev1.testpeso.com/zoomApi/redirect';
                window.open('https://zoom.us/oauth/authorize?client_id='+zoomId+'&response_type=code&redirect_uri='+redirectUrl,'zoomLoginWin','width=500,height=600')
            },
            settingSns() {
                this.snsList = [];
                for (const key in this.getSns) {
                    let img = '';
                    let title = '';
                    switch (key) {
                        case 'gl':
                            img = require('@/assets/img/ico_sns1.svg')
                            title = '구글로 로그인'
                            break;
                        case 'fb':
                            img = require('@/assets/img/ico_sns2.svg')
                            title = '페이스북으로 로그인'
                            break;
                        case 'ka':
                            img = require('@/assets/img/ico_sns4.svg')
                            title = '카카오톡으로 로그인'
                            break;
                        case 'nv':
                            img = require('@/assets/img/ico_sns3.svg')
                            title = '네이버로 로그인'
                            break;


                    }
                    if (this.getSns[key].key) {
                        let item = {
                            img: img,
                            code: key,
                            title: title
                        }
                        this.snsList.push(item);
                    }
                }
            },
            zoomLoginResult(params) {
                const result = params.status;
                if (result == 'success') {
                    AUTH.setSession(params.result)
                    params = {
                        message: '로그인 되었습니다',
                        doAction: () => {
                            location.href = '/';
                        }
                    };

                } else {
                    params = {
                        message: '줌로그인에 실패하였습니다',
                    };
                }
                this.$eventBus.$emit('modalOpen', params);
            },
            snsLogin(snsCode) {
                this.snsCode = snsCode;
                let redirect_uri = '';
                let url;
                let size;

                switch (snsCode) {
                    case 'ka':  // 카카오 로그인

                        redirect_uri = this.getSns[snsCode].redirectUrl;
                        url = 'https://kauth.kakao.com/oauth/authorize?response_type=code&client_id=' + this.getSns[snsCode].key + '&redirect_uri=' + redirect_uri;
                        size = 'width=450,height=600,left=100,top=100'

                        break;
                    case 'nv':
                        redirect_uri = this.snsInfo[snsCode].redirectUrl;
                        url = 'https://nid.naver.com/oauth2.0/authorize?response_type=code&client_id=' + this.getSns[snsCode].key + '&redirect_uri=' + redirect_uri;
                        size = 'width=600,height=600,left=100,top=100'
                        break;
                    case 'fb':
                        redirect_uri = this.snsInfo[snsCode].redirectUrl;
                        url = 'https://facebook.com/v2.11/dialog/oauth?client_id=' + this.getSns[snsCode].key + '&redirect_uri=' + redirect_uri;
                        size = 'width=600,height=600,left=100,top=100'

                        break;

                }
                const popup = window.open(url, 'snsLoginWin', size);
                if (location.hostname == 'localhost') {
                    setTimeout(() => {
                        popup.postMessage("check", "http://shop.riverflow.co.kr");
                    }, 1000);
                }


            },
            validate() {
                this.$v.$touch();
                if (!this.$v.$anyError) {
                    const frm = {
                        uid: this.uid,
                        upass: this.upass
                    }
                    if (this.insertFlag) return;
                    this.insertFlag = true;

                    this.$eventBus.$emit('overlay', 'open');
                    apiService.post(API_URL_LOGIN, frm, (res) => {

                        this.$eventBus.$emit('overlay', 'hide');
                        let result = res.status;
                        let params = {}
                        if (result == 'success') {
                            AUTH.setSession(res)
                            params = {
                                message: '로그인 되었습니다',
                                doAction: () => {
                                    location.href = '/';
                                }
                            };

                        } else {
                            let code = res.code;
                            let msg = '';
                            switch (code) {
                                case 'notauth':
                                    msg = '미인증된 회원입니다'
                                    break;
                                case 'uout':
                                    msg = '탈퇴한 회원 입니다'
                                    break;
                                default:
                                    msg = '잘못된 아이디 또는 비밀번호입니다'
                                    break;
                            }
                            this.insertFlag = false;
                            params = {
                                message: msg,
                            };

                        }
                        this.$eventBus.$emit('modalOpen', params);

                    })
                }
            },
            findPass() {
                this.$router.push('/memberFindPass')
            },
            snsLoginResult(params) {
                // console.log(params.data)
                if (params) {
                    let alertparams = {};
                    switch (params.status) {
                        case 'login':
                            AUTH.setSession(params.data)
                            alertparams = {
                                message: '로그인 되었습니다',
                                doAction: () => {
                                    location.href = '/';
                                }
                            };
                            this.$eventBus.$emit('modalOpen', alertparams);
                            break;
                        case 'message':
                            alertparams = {
                                message: '잘못된 접근입니다',
                            };
                            this.$eventBus.$emit('modalOpen', alertparams);
                            break;
                        case 'required':
                            const nextFrm = params.data;
                            nextFrm.joinType = 'sns';
                            sessionStorage.setItem('nextMemberInfo', JSON.stringify(nextFrm));
                            this.$router.push('/memberJoin');
                            break;
                        case 'agree':

                            const frm = params.data;
                            frm.joinType = 'sns';
                            sessionStorage.setItem('tempMemberInfo', JSON.stringify(frm));
                            this.$router.push('/joinAgree');
                            break;
                    }

                }

            }
        },
        destroyed() {
            this.$eventBus.$off('snsLoginResult');

        }

    }
</script>

<style lang="scss" scoped>
    .login-container {
        width: 636px;
        margin: 0 auto;
        padding: 36px 24px;
        background: #fff;

        .login-title {
            color: #333;
            font-size: 24pt;
            font-weight: bold;
            margin-bottom: 35px;
            padding-bottom: 8px;
            text-align: center;
        }

        .w-full {
            width: 100%;
            margin: auto;

            .input-box {
                .label {
                    font-size: 14px;
                    color: #333;
                    padding-bottom: 5px;
                    font-weight: 600;
                }

                width: 100%;
                margin: auto;
            }
        }

        .button-row {
            width: 100%;
            padding: 10px 0 25px 0;

            .join-btn {
                color: #333;
                font-size: 14px;
                text-decoration: underline;
                cursor: pointer;
            }

            .guide {
                color: #999999;
                font-size: 14px;
                padding-right: 20px;
            }
        }

        .sub-title {
            color: #232323;
            font-weight: 600;
            font-size: 16px;
            text-align: center;
        }

        .find-box {
            padding-bottom: 15px;

            .right {
                text-align: center;
                color: #999999;
                font-size: 14px;
                cursor: pointer;
                text-decoration: underline;
            }
        }

        .divide-line {
            width: 100%;
            margin: 20px 0;
            border: 1px solid #DCDCDC;
            height: 0;
        }

        .sns-box {
            .sns-ul {
                padding: 0;
                margin: 0;

                li {
                    cursor: pointer;
                    width: 100%;
                    height: 48px;
                    border: solid 1px #dcdcdc;
                    border-radius: 5px;
                    margin: 10px 0;
                    text-align: center;
                    line-height: 48px;

                    img {
                        margin: 8px 10px 0 0;
                    }

                    span {
                        position: relative;
                        top: -8px;
                        font-size: 18px;
                        color: #333;
                        font-weight: bold;

                    }
                }
            }
        }
    }
    @media screen and (min-width: 450px) and (max-width: 1259px) {
        .login-container {
            width: 90%;
            margin: 40px auto;
        }

    }

    @media screen and (max-width: 450px) {
        .login-container {
            width: 100%;
            margin:50px 0 0 0;
            padding: 36px 20px;
        }
    }
</style>
